import imageService from './services/apiService';
import imageCards from '../templates/img-card.hbs';
import * as basicLightbox from 'basiclightbox';
import '../../node_modules/basiclightbox/dist/basicLightbox.min.css';

const refs = {
  searchForm: document.querySelector('#search-form'),
  gallery: document.querySelector('#gallery'),
  btnLoad: document.querySelector('#load-btn'),
};

refs.searchForm.addEventListener('submit', handleSearchForm);
refs.gallery.addEventListener('click', handleGallery);
refs.btnLoad.addEventListener('click', handleBtnLoad);

function handleSearchForm(e) {
  e.preventDefault();

  imageService.page;

  const form = e.currentTarget;
  const input = form.elements.query;

  clearGallery();

  imageService.resetPage();
  imageService.searchQuery = input.value;
  fetchImages();

  input.value = '';
  refs.btnLoad.classList.remove('visually-hidden');
}

function handleGallery(e) {
  const largeImg = e.target.dataset.src;
  const image = `<img src="${largeImg}" width="800" height="600">`;
  const instance = basicLightbox.create(image);
  instance.show();
}

function handleBtnLoad() {
  if (imageService.page > 1) {
    imageService.fetchImages().then(images => {
      insertGalleryItems(images);
      window.scrollTo({
        top: window.scrollY + window.innerHeight,
        behavior: 'smooth',
      });
    });
  }
}

function fetchImages() {
  imageService
    .fetchImages()
    .then(images => {
      insertGalleryItems(images);
    })
    .catch(error => console.warn(error));
}

function insertGalleryItems(images) {
  const markup = imageCards(images);

  refs.gallery.insertAdjacentHTML('beforeend', markup);
}

function clearGallery() {
  refs.gallery.innerHTML = '';
}
