const baseURL = 'https://pixabay.com/api/';

export default {
  keyAPI: '16353220-505ad01d611a0174a04cba4dd',
  page: 1,
  query: '',

  fetchImages() {
    const params = `?image_type=photo&orientation=horizontal&q=${this.query}&page=${this.page}&per_page=12&key=${this.keyAPI}`;
    return fetch(baseURL + params)
      .then(response => response.json())
      .then(data => {
        this.incrementPage();
        return data.hits;
      });
  },

  get searchQuery() {
    return this.query;
  },
  set searchQuery(string) {
    this.query = string;
  },
  incrementPage() {
    this.page += 1;
  },
  resetPage() {
    this.page = 1;
  },
};
